* {
  margin: 0;
  padding: 0;
}

.grid {
  width: 100%;
  height: 100vh;
  margin-right: auto;
  margin-left: auto;
  display: grid;
  grid-template-columns: 1fr 5fr 2fr 1fr;
  grid-template-rows: 100px auto auto;
  grid-column-gap: 2vw;
  font-family: "Playfair Display", serif;
  font-size: 2rem;
}

.grid h1 {
  grid-column: 1;
  grid-row: 2/-1;
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  align-self: flex-start;
  justify-self: flex-end;
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
}

.grid main {
  grid-column: 2;
  grid-row: 2/-1;
  line-height: 1.3;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
}

.grid p {
  margin-bottom: 1em;
}

.grid a {
  font-style: italic;
  color: #DC5F51;
}

.grid a:hover {
  color: #555555;
}

.grid img {
  float: right;
  max-width: 260px;
  height: auto;
  shape-outside: circle();
  border-radius: 50%;
  margin: 0.2em;
}

.grid aside {
  grid-column: 3;
  grid-row: 3;
}

@media (min-width: 1200px) {
  .grid {
    max-width: 1140px;
  }
}
