@import url("https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans:300");

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.25rem;
  font-weight: 300;
  color: #999;
}

a {
  text-decoration: none;
}

a,
a:hover {
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  line-height: 1.2;
}

h1 {
  font-size: 3.157em;
  letter-spacing: -0.05em;
}

h2 {
  font-size: 2.369em;
}

h3 {
  font-size: 1.777em;
}

h4 {
  font-size: 1.333em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 0.75em;
}
